import React from "react";
import Nav from "../Components/Nav";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet";

const AboutMe = () => {
  const variants = {
    initial: {
      opacity: 0,
      transition: { duration: 3 },
      y: 300,
    },
    visible: {
      opacity: 1,
      y: 0,
    },
    exit: {
      opacity: 0,
      transition: { duration: 3 },
      y: -300,
    },
  };

  const imgPopUp = {
    initial: {
      opacity: 0,
      transition: { duration: 7 },
      y: 0,
    },
    visible: {
      opacity: 1,
      transition: { duration: 1 },
      y: 0,
    },
    exit: {
      opacity: 0,
      transition: { duration: 7 },
    },
  };

  return (
    <div>
      <Helmet>
        <title>Mdv |  propos</title>
        <meta
          name="description"
          content="Je vous partage ici la liste non-exhaustive des diffrents langages que je pratique, tels que React et Nextjs, ou encore FramerMotion. Venez dcouvrir qui je suis et comment je travaille."
        />
        <meta
          property="og:title"
          content="Mdv | Dveloppeuse web freelance et fullremote  Toulouse"
        />
        <meta
          property="og:description"
          content="Portfolio d'une jeune dveloppeuse web front-end base  Toulouse, Mdv, qui a choisit de lutter  sa faon contre les ingalits, en vous proposant des tarifs prfrentiels selon votre profil."
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="" />
        <meta property="og:image" content="/BAN.JPG" />
      </Helmet>
      <Nav />
      <motion.div
        initial="initial"
        animate="visible"
        exit="exit"
        variants={variants}
        className="content"
      >
        <h2>Ce que je suis, ce que je fais</h2>
        <p>
          Aprs m'tre aventure sur de nombreuses routes professionnelles, j'ai
          affin anne aprs anne, la connaissance de ce qui me correspondait
          rellement. Apporter quelque chose  autrui, construire du bout de mes
          doigts, faire de mes projets des pops, voil ce que je voulais faire
          de ma carrire ! C'est ainsi que le{" "}
          <strong>dveloppement web </strong> s'est impos  moi. Je m'applique
          aujourd'hui  parler les langues de mon navigateur, telles que{" "}
          <strong>HTML, CSS, JavaScript, React, Nextjs </strong> ou encore
          <strong> FramerMotion </strong>, qui sont les protagonistes de ce
          dialogue et que je me propose de mettre  votre service. Dans
          l'enthousiasme d'changer davantage avec vous sur le sujet, n'hsitez
          pas  me <a href="/contact">contacter.</a>
        </p>
      </motion.div>

      <motion.div
        className="aboutme"
        initial="initial"
        animate="visible"
        exit="exit"
        variants={imgPopUp}
      ></motion.div>
    </div>
  );
};

export default AboutMe;
