import React from "react";
import Cards from "../Components/Cards";
import Nav from "../Components/Nav";
import { Helmet } from "react-helmet";

const Creations = () => {
  return (
    <div>
      <Nav />
      <section id="second">
        <Helmet>
          <title>Mdv | Crations</title>
          <meta
            name="description"
            content="Voici diffrents exemples de ce que je peux raliser en tant que dveloppeuse web front-end, accompagns du code source et du dtails des divers langages utiliss, tels que Javascript, React ou encore Nextjs."
          />
          <meta
            property="og:title"
            content="Mdv | Dveloppeuse web freelance et fullremote  Toulouse"
          />
          <meta
            property="og:description"
            content="Portfolio d'une jeune dveloppeuse web front-end base  Toulouse, Mdv, qui a choisit de lutter  sa faon contre les ingalits, en vous proposant des tarifs prfrentiels selon votre profil."
          />
          <meta property="og:type" content="website" />
          <meta property="og:url" content="" />
          <meta property="og:image" content="/BAN.JPG" />
        </Helmet>
        <Cards />
      </section>
    </div>
  );
};

export default Creations;
